<template>
<CRow>
    <CCol col="12" class="mb-5">
        <CRow>
            <CCol col="8">
                <h3 class="font-weight-bold text-uppercase mb-1">modul aplikasi Formulir</h3>
                <p class="text-primary text-sm-left mb-0">{{$options.identitas[0].instansi}}</p>
                <p class="text-primary text-sm-left">{{$options.identitas[0].provinsi}}</p>
            </CCol>
            <CCol>
                <div class="text-right mt-5 mr-5">
                    <router-link :to="{ path:'/settings/modul'}" class="btn btn-primary text-white">Halaman Modul</router-link>
                </div>
            </CCol>
        </CRow>
    </CCol>
    <CCol col="12">
        <div class="wrapperModul p-3">
            <CRow>
                <CCol sm="2">
                    <CInput label="Nama Modul" v-model.trim="$v.modul.name.$model" :isValid="$v.modul.name.$dirty ? !$v.modul.name.$error : null" placeholder="Ketik Nama Modul" />
                </CCol>
                <CCol sm="1">
                    <CSelect :isValid="$v.modul.type.$dirty ? !$v.modul.type.$error : null" :options="options.select" :value.sync="$v.modul.type.$model" :lazy="true" invalidFeedback="asdas" label="Modul" placeholder="Type" @change="typeModul()" />
                </CCol>
                <CCol sm="2">
                    <CInput label="URL" v-model.trim="$v.modul.url.$model" :isValid="$v.modul.url.$dirty ? !$v.modul.url.$error : null" placeholder="Ketik Url Route" />
                </CCol>
                <CCol sm="1">
                    <CInput label="Icon" v-model.trim="$v.modul.icon.$model" :isValid="$v.modul.icon.$dirty ? !$v.modul.icon.$error : null" placeholder="Ketik Icon" />
                </CCol>
                <CCol sm="2" v-if="parentShow">
                    <CSelect @change="selectModul()" :isValid="$v.modul.parent.$dirty ? !$v.modul.parent.$error : null" :options="optionsParent" :value.sync="$v.modul.parent.$model" :lazy="true" invalidFeedback="asdas" label="Modul Parent" placeholder="Modul Parent" />
                </CCol>

                <CCol sm="3">
                    <CSwitch class="mr-1 mt-4 pt-1" color="primary" :checked.sync="modul.active" v-model="modul.active" />
                </CCol>
            </CRow>
            <CRow>
                <CCol col="12">
                    <CButton color="primary" class="text-white" @click="ToDatabase()">{{type}}</CButton>
                </CCol>
            </CRow>
        </div>
        <div v-if="modul.child" >
            <h5 class="font-weight-bold text-uppercase mt-3 mb-1" v-if="modul.child.length">Modul Child</h5>
            <div class="wrapperModul p-3 mt-2" v-if="modul.child.length">
                <CRow v-for="(m,mIndex) in modul.child" :key="mIndex">
                    <CCol sm="2">
                        <CInput label="Nama Modul" v-model.trim="m.name" placeholder="Ketik Nama Modul" />
                    </CCol>
                    <CCol sm="2">
                        <CInput label="URL" v-model.trim="m.url" placeholder="Ketik Url Route" />
                    </CCol>
                    <CCol sm="1">
                        <CInput label="Icon" v-model.trim="m.icon" placeholder="Ketik Icon" />
                    </CCol>
                    <CCol sm="2">
                        <CSelect @change="selectModul()" :options="optionsParent" :value.sync="m.modul_id" :lazy="true" invalidFeedback="asdas" label="Modul Parent" placeholder="Modul Parent" />
                    </CCol>
                    <CCol sm="3">
                        <CSwitch class="mr-1 mt-4 pt-1" color="primary" :checked.sync="m.active" v-model="m.active" />
                    </CCol>
                    <CCol sm="2" class="mt-4">
                        <CButton size="sm" color="info" class="mr-1" @click="dialogEdit = true, selectedChild = mIndex, selectedChildType = 'update'">
                            <CIcon name="cil-color-border" class="mr-1"></CIcon> Edit
                        </CButton>
                        <CButton color="light" class="text-danger font-weight-bold" @click="dialogDelete = true, selectedChild = mIndex, selectedChildType= 'delete' ">
                            <CIcon name="cil-trash" class="mr-0"></CIcon> Hapus
                        </CButton>
                    </CCol>
                </CRow>
            </div>
        </div>
    </CCol>
    <CModal title="Informasi" color="info" :show.sync="dialogEdit" :closeOnBackdrop="false">
        Edit Data Modul <span class="font-weight-bold" v-if="selectedChild != null">{{modul.child[selectedChild].name}} ?</span>
        <template slot="footer">
            <CButton color="info" @click="ToDatabaseChild(selectedChild)">Edit</CButton>
        </template>
    </CModal>
    <CModal title="Informasi" color="danger" :show.sync="dialogDelete" :closeOnBackdrop="false">
        Hapus Data ini <span class="font-weight-bold" v-if="selectedChild != null">{{modul.child[selectedChild].name}} ?</span>
        <template slot="footer">
            <CButton color="danger" @click="ToDatabaseChild(selectedChild)">Hapus</CButton>
        </template>
    </CModal>
</CRow>
</template>

<script>
import identitas from '@/containers/_identitas'
import tableta from '@/views/table/table'
import {
    required
} from 'vuelidate/lib/validators'
const axios = require('axios');
export default {
    identitas,
    components: {
        tableta
    },
    data() {
        return {
            selectedChild : null,
            selectedChildType : 'update',
            dialogEdit : false,
            dialogDelete : false,
            parentShow: false,
            urlPost: this.$options.identitas[0].url + '/modul?type=dataById',
            modul: {
                modul_id: null,
                name: null,
                url: null,
                type: null,
                active: false,
                icon: null,
                parent: null,
            },
            options: {
                select: [{
                        label: "Single",
                        value: "single",
                    }, {
                        label: "Parent",
                        value: "parent",
                    },
                    {
                        label: "Child",
                        value: "child",
                    },
                ],
                parent: [{
                    label: "Single",
                    value: "single",
                }, {
                    label: "Parent",
                    value: "parent",
                }, ],
            },
            type: this.$route.params.type === 'edit' ? "update" : "insert",
        }
    },
    computed: {
        optionsParent() {
            return this.options.parent.map((header, index) => {
                return {
                    label: header.name,
                    value: header.modul_id,
                };
            });
        }
    },
    validations: {
        modul: {
            name: {
                required
            },
            url: {
                required
            },
            type: {
                required
            },
            icon: {
                required
            },
            parent: {
                required
            },

        }
    },
    created() {
        this.getData()
        this.getDataByType()
    },
    methods: {
        typeModul() {
            this.modul.type === 'child' ? (this.modul.parent = null, this.parentShow = true, this.urlPost = this.$options.identitas[0].url + '/modul/child?type=' + this.type) : (this.modul.parent = 0, this.parentShow = false, this.urlPost = this.$options.identitas[0].url + '/modul?type=' + this.type)
        },
        selectModul() {
            this.modul.modul_id = this.modul.parent
        },
        getData() {
            axios.post(this.$options.identitas[0].url + '/modul?type=dataById', {
                    id: this.$route.params.id
                })
                .then((e) => {
                    e.data.code === "404" ? this.$router.push({
                        path: '/pages/404'
                    }) : (this.modul = e.data.data)

                })
                .catch(function (error) {
                    // handle error
                    console.log(error);
                })
        },
        getDataByType() {
            axios.post(this.$options.identitas[0].url + '/modul?type=dataByType', {
                    id: this.$route.params.id
                })
                .then((e) => {
                    this.options.parent = e.data

                })
                .catch(function (error) {
                    // handle error
                    console.log(error);
                })
        },
        ToDatabase() {
            this.$v.$touch()
            if (!this.$v.$invalid) {
                axios.post(this.$options.identitas[0].url + '/modul?type=' + this.type, {
                        data: this.modul
                    })
                    .then((e) => {
                        this.$notify({
                            group: 'foo',
                            title: 'Information',
                            text: this.$route.params.type === 'edit' ? "Data Berhasil Di Ubah" : "Data Berhasil Di Tambahkan",
                        });

                        this.$router.push({
                            path: '/settings/modul'
                        })
                    })
                    .catch(function (error) {
                        // handle error
                        console.log(error);
                    })
            }
        },
        ClickedOnChild(parent, child, type) {
            this.table.data[parent].crud[type] = true
            this.table.data[parent].crud['read'] = true
            this.table.data[parent].child[child].crud['read'] = true
        },
        ToDatabaseChild(index) {
            axios.post(this.$options.identitas[0].url + '/modul/child?type=' + this.selectedChildType, {
                    data: this.modul.child[index]
                })
                .then((e) => {
                    this.$notify({
                        group: 'foo',
                        title: 'Information',
                        text: e.data.message
                    });
                    this.getData()
                    this.dialogEdit = false
                    this.dialogDelete = false
                })
                .catch(function (error) {
                    // handle error
                    console.log(error);
                })
        }
    }

}
</script>

<style>
.wrapperModul {
    border: 1px solid #dfdfdf;
    border-radius: 5px;

}

.float {
    position: fixed;
    width: 120px;
    bottom: 80px;
    right: 40px;
    color: #FFF;
    text-align: center;
    box-shadow: 2px 2px 3px #999;
}
</style>
